
* {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
   padding: 60px;
}

input {
  font-size: 1.2rem;
  color: gray;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.card { 
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10 px;
  margin-right: 10px;
  overflow: hidden;
}

.card p {
   padding: 5px;
   text-align: center;
}

.card img {
  max-height: 170px;
}


